<template>
    <div v-if="loading" class="d-flex justify-content-center mb-5">
        <div class="spinner-border spinner-border-sm text-danger"></div>
    </div>
    <template v-else>
        <Breadcrumb :program="program" />
        <section id="select-info">
            <div class="container">
                <Cover :program="program" />
                <div id="buy-process" class="my-5">
                    <ul class="nav justify-content-md-evenly justify-content-center align-items-center">
                        <li class="nav-item buy-process__item col active">
                            <router-link :to="{
                                name: 'select_time',
                                params: {
                                    id: route.params.program_id,
                                }
                            }" class="nav-link buy-process__link px-sm-3 px-2">
                                <div class="buy-process--shape">
                                    <span class="buy-process--shape-num">1</span>
                                </div>
                                <p class="buy-process--text">انتخاب سانس</p>
                            </router-link>
                        </li>
                        <li class="nav-item buy-process__item col active">
                            <router-link :to="{
                                name: 'seats',
                                params: {
                                    programId: route.params.program_id,
                                    sanId: route.params.san_id
                                }
                            }" class="nav-link buy-process__link px-sm-3 px-2">
                                <div class="buy-process--shape">
                                    <span class="buy-process--shape-num">2</span>
                                </div>
                                <p class="buy-process--text">انتخاب صندلی</p>
                            </router-link>
                        </li>
                        <li class="nav-item buy-process__item col active">
                            <div class="nav-link buy-process__link px-sm-3 px-2">
                                <div class="buy-process--shape">
                                    <span class="buy-process--shape-num">3</span>
                                </div>
                                <p class="buy-process--text">تکمیل اطلاعات</p>
                            </div>
                        </li>
                        <li class="nav-item buy-process__item col active">
                            <div class="nav-link buy-process__link px-sm-3 px-2">
                                <div class="buy-process--shape">
                                    <span class="buy-process--shape-num">4</span>
                                </div>
                                <p class="buy-process--text">پرداخت</p>
                            </div>
                        </li>
                        <li class="nav-item buy-process__item col active">
                            <div class="nav-link buy-process__link px-sm-3 px-2">
                                <div class="buy-process--shape">
                                    <span class="buy-process--shape-num">5</span>
                                </div>
                                <p class="buy-process--text">دریافت بلیت</p>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="select-ticket mb-5">
                    <h2 class="select-ticket__heading d-flex justify-content-start align-items-center mb-2">
                        <img src="../../assets/images/ticket2.svg" alt="" class="me-2" width="24px" height="26px">
                        بلیت خریداری شده
                    </h2>
                    <ul class="select-ticket__list my-5">
                        <li v-for="(ticket, key) in tickets" :key="key"
                            class="select-ticket__item w-100 d-flex justify-content-between align-items-center px-0">
                            <div class="select-ticket__item--receipt-icon d-md-block d-none">
                                <img src="../../assets/images/receipt2.svg" alt=""
                                    class="select-ticket__item--receipt-img" width="24px" height="24px">
                            </div>
                            <div class="select-ticket__paper row mx-3">
                                <div class="col-lg-4 col-md-6 py-3 position-relative">
                                    <div class="select-ticket__column-one pb-md-0 pb-3 pe-2">
                                        <div class="d-flex align-items-center">
                                            <div class="select-ticket__number d-flex justify-content-between">
                                                <p class="p-0">تعداد بلیط:</p>
                                                <span class="p-0">{{ order.tickets_count }}</span>
                                                <span class="p-0">{{ key + 1 }}</span>
                                            </div>
                                            <img :src="ticket.QRCODE" alt="" class="select-ticket--qr col"
                                                style="max-width: 120px; max-height: 120px;margin: 0 22px 17px 10px;">
                                            <div class="select-ticket__customer-info col ps-1">
                                                <div class="select-ticket__customer-info--item d-flex flex-wrap">
                                                    <span class="select-ticket__customer-info--span pe-1">نام
                                                        خریدار:</span>
                                                    <span class="select-ticket__customer-info--span-bold">{{ user.name +
                                                        ' ' + user.family }}</span>
                                                </div>
                                                <div class="select-ticket__customer-info--item d-flex flex-wrap mb-3">
                                                    <span class="select-ticket__customer-info--span pe-1">تلفن
                                                        همراه:</span>
                                                    <span class="select-ticket__customer-info--span">{{ user.mobile
                                                        }}</span>
                                                </div>
                                                <div class="select-ticket__customer-info--item d-flex flex-wrap">
                                                    <span class="select-ticket__customer-info--span pe-1">شماره
                                                        فاکتور:</span>
                                                    <span class="select-ticket__customer-info--span">{{ factor.number
                                                        }}</span>
                                                </div>
                                                <div class="select-ticket__customer-info--item d-flex flex-wrap">
                                                    <span class="select-ticket__customer-info--span-bold pe-1">شماره
                                                        بلیط:</span>
                                                    <span class="select-ticket__customer-info--span-bold">{{
                                                        ticket.number_ticket }}</span>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="select-ticket__seating-position d-flex justify-content-around ms-3">
                                            <div class="d-flex">
                                                <span class="select-ticket__seating-position--text">ردیف</span>
                                                <span class="select-ticket__seating-position--number">{{
                                                    ticket.number_row }}</span>
                                            </div>
                                            <div class="d-flex">
                                                <span class="select-ticket__seating-position--text">صندلی</span>
                                                <span class="select-ticket__seating-position--number">{{
                                                    ticket.number_chair }}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-lg-4 col-md-6 d-flex align-items-center py-3 ps-md-0 ps-3">
                                    <div class="select-ticket__concert-info w-100">
                                        <p class="select-ticket__concert-info--name mb-1">{{ program.title }}</p>
                                        <p
                                            class="select-ticket__concert-info--item d-flex justify-content-start align-items-center mb-1">
                                            <img src="../../assets/images/Location-black.svg" alt="" class="me-1"
                                                width="9.17px" height="11px">
                                            {{ program.hall.city.city_name + ' ' + program.hall.hall_name }}
                                        </p>
                                        <p
                                            class="select-ticket__concert-info--item d-flex justify-content-start align-items-center mb-1">
                                            <img src="../../assets/images/Calendar-black.svg" alt="" class="me-1"
                                                width="11.3px" height="12.2px">
                                            {{ san.start_date.split(' ')[0] }}
                                        </p>
                                        <p
                                            class="select-ticket__concert-info--item d-flex justify-content-start align-items-center">
                                            <img src="../../assets/images/Clock-black.svg" alt="" class="me-1"
                                                width="11.7px" height="11.7px">
                                            {{ san.start_date.split(' ')[1] }}
                                        </p>
                                        <div
                                            class="select-ticket__concert-info--total d-flex flex-wrap justify-content-between align-items-center pt-4">
                                            <p class="select-ticket__concert-info--total-text text-start">مبلغ کل با
                                                احتساب ۹٪ ارزش افزوده:</p>
                                            <p class="select-ticket__concert-info--total-price text-end">{{
                                                factor.total.toLocaleString() }} تومان</p>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-lg-4 d-lg-block d-none p-0 select-ticket__column-three">
                                    <div class="select-ticket__performer">
                                        <img :src ="APP_URL + program.cover" alt=""
                                            class="select-ticket__performer--img">
                                        <span class="select-ticket__performer-overlay"></span>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                    <div class="select-ticket__more ms-auto px-3">
                        <div class="d-flex flex-wrap justify-content-sm-between justify-content-center">
                            <ul class="nav justify-content-start align-items-end">
                                <li class="nav-item me-3 mb-2">
                                    <a :disabled="downloadLoading" @click.prevent="download()"
                                        class="select-ticket__more--download nav-link d-flex justify-content-start align-items-center"
                                        href="">
                                        <img src="../../assets/images/document-download.svg" alt="" class="me-2"
                                            width="20px" height="20px">
                                        <div v-if="downloadLoading" class="spinner-border spinner-border-sm"></div>
                                        <span v-else> دانلود pdf</span>
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </template>
</template>
<script setup>
import { ref, onMounted, computed } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import Cover from './Cover.vue'
import { useCartStore } from '../../store/cart'
import { useHead } from '@vueuse/head'
const cartStore = useCartStore()
const route = useRoute();
let loading = ref(false)
let downloadLoading = ref(false)
let program = ref('');
let san = ref('');
let order = ref('');
let factor = ref('');
let tickets = ref([]);
let user = ref('');
import paymentsApi from '@/axios/api'
import { handleError } from '@/utils/helpers';
import Breadcrumb from '../Breadcrumb.vue';
const APP_URL = process.env.VUE_APP_BASE_URL
onMounted(async () => {
    loading.value = true
    try {
        const apiUrl = `/programs/${route.params.program_id}/sans/${route.params.san_id}/select-ticket/orders/${route.params.order_id}`;
        const response = await paymentsApi.get(apiUrl);
        if (response.data.status === 'success') {
            program.value = response.data.data.program
            san.value = response.data.data.san
            order.value = response.data.data.order
            factor.value = response.data.data.factor
            tickets.value = response.data.data.tickets
            user.value = response.data.data.user
            useHead({
                // Can be static or computed
                title: computed(() => `${program.value.title} - ${program.value.artist.name} ${program.value.artist.family}`),
                meta: [
                    {
                        name: `description`,
                        content: computed(() => {
                            return program.value.title + " " + program.value.hall.title + " " + program.value.hall.city.title
                        }),
                    },
                ],

            })
        }
    } catch (err) {
        toast.error(handleError(err).join(', '), {
            timeout: 5000
        });
    } finally {
        loading.value = false
        cartStore.clearCart()
    }
})
async function download() {
    downloadLoading.value = true;
    try {
        const apiUrl = `programs/${route.params.program_id}/sans/${route.params.san_id}/tickets/orders/${route.params.order_id}/download`;
        const response = await paymentsApi.get(apiUrl, {
            responseType: "blob",
        });
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", 'tickets.pdf');
        document.body.appendChild(link);
        link.click();
        link.remove();
        downloadLoading.value = false;
    } catch (err) {
        toast.error(handleError(err).join(', '), {
            timeout: 5000
        });
    } finally {
        downloadLoading.value = false
    }
}
</script>